* {
  font-family: "Factor A", sans-serif;
  font-weight: 400;
  font-style: normal;
}
html {
  overflow-x: hidden 
}
body {
  padding-right: 0 !important;
  color: #000;
}

html .--fw700 {
  font-weight: 700;
}
html .--fw500 {
  font-weight: 500;
}
.--uppercase {
  text-transform: uppercase;
}

.page-btn {
  @include page-btn();
}
.page-link {
  @include page-link();
}
.page-h2 {
