  }
  .programms__headline {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-bottom: 50px;
    .programms__header {
      margin: 0;
    }
  }
  .programms__header {

  }
  .programms__headline-text {
    color: #a6a7ab;
    margin: 0;
  }

  .programs__filters {
    display: grid;
    grid-template-columns: 1fr 3fr;
    gap: 5px;
    align-items: start;
    margin-bottom: 70px;
  }
  .programs__filters-left {

  }
  .programs__filters-trigger {
    display: flex;
    justify-content: start;
    align-items: center;
    color: #A6A7AB;
    cursor: pointer;

    img {
      margin-left: 5px;
    }
  }
  .programs__filters-right {
    
  }
  .programs__filters-form {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 8px;
  }
  .programs__filters-form__col {
    display: flex;
    flex-direction: column;
    align-items: start;
  }
  .programs__filters-form__header {
    display: block;
    margin-bottom: 14px;
    color: #A6A7AB;
  }
  .programs__filters-form__label {
    position: relative;
    display: flex;
    justify-content: start;
    align-items: center;
    cursor: pointer;

    &:not(:last-child) {
      margin-bottom: 13px;
    }
  }
  .programs__filters-form__check {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    pointer-events: none;

    &:checked + .programs__filters-form__custom {
      background: #D64E34;
    }
  }
  .programs__filters-form__custom {
    width: 11px;
    height: 11px;
    background: #d2d3d5;
    border-radius: 2px;
    flex-shrink: 0;
    margin-right: 8px;

    transition: .3s;
  }
  .programs__filters-form__text {
    line-height: 100%;
    display: inline-block;
  }

  .programs__empty.programms__items {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 150px 36px;
    background: #fff;
    border: 1px solid #a6a7ab;
    border-radius: 10px 10px 0 10px;
  }
  .programs__empty-text {
    color: #a6a7ab;
    margin-bottom: 22px;
    text-align: center;
  }
  .programs__empty-btn {
    color: #a6a7ab;
    border-color: #a6a7ab;
  }

  .programms__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 20px 10px;

    
  }
  .programms__item {
    color: #fff;
    position: relative;
    padding: 25px;
    border-radius: 10px;
    overflow: hidden;
    min-height: 391px;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .programms__item-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    object-fit: cover;
  }
  .programms__item-top {
    display: flex;
    justify-content: space-between;
    align-items: start;
  }
  .programms__item-type {
    font-weight: 700;
  }
  .programms__item-tags {

  }
  .programms__item-tag {
    display: block;
    padding: 4px 15px;
    border: 1px solid #fff;
    border-radius: 5px;

    &:not(:last-child) {
      margin-bottom: 5px;
    }
    &.--orange {
      background: #d64e34;
    }
  }
  .programms__item-body {

  }
  .programms__item-author {
    display: block;
    margin-bottom: 33px;
    span {
      display: block;
      font: inherit;
      font-weight: inherit;
      font-weight: 700 !important;
    }
  }
  .programms__item-date {
    color: #000;
    display: block;
    margin-bottom: 9px;
  }
  .programms__item-place {
    display: block;
  }

  .programms__items-link {
    margin-top: 30px;
  }

  .masters__item {

  }
  .masters__item-img {
    border-radius: 10px;
    overflow: hidden;
    margin-bottom: 20px;

    img {
      width: 100%;
      height: 100%;
    }
  }
  .masters__item-name {
    display: block;
    margin-bottom: 5px;
  }
  .masters__item-post {
    display: block;
    color: #a6a7ab;
  }
}

