.smi {
  padding: 75px 0;
  .smi__wrapper {

  }
  .smi__header {
    margin-bottom: 50px;
    margin-top: 0;
  }
  .smi__items {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 10px;
  }
  .smi__item {
    padding-top: 100%;
    position: relative;
    color: #000;
    display: block;
    padding: 25px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow: hidden;

    &::before {
      content: '';
      width: 100%;
      display: block;
      padding-top: 100%;
    }

    &.--color-white {
      color: #fff;
    }

    &:nth-child(1) {
      border-radius: 10px 0 10px 10px;
    }
    &:nth-child(2) {
      border-radius: 10px 10px 0 10px;
      .smi__item-text {
        color: #fff;
      }
    }
    &:nth-child(3) {
      border-radius: 10px 10px 10px 0;
    }
    &:nth-child(4) {
      border-radius: 0 10px 10px 10px;
    }
  }
  .smi__item-img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
  }
  .smi__item-name {
    position: absolute;
    top: 25px;
    left: 0;
    padding: 0 25px;
  }
  .smi__item-text {
    color: #a6a7ab;
    position: absolute;
    bottom: 25px;
    left: 0;
    padding: 0 25px;
  }
}

