    .cost__form-type {
      padding-left: 19px;
      &:first-child {
        margin-top: 10px;
      }
      &:not(:last-child) {
        margin-bottom: 7px;
      }
    }
    .cost__form-desc {
      font-size: 10px;
      color: #d64e34;
    }
  }
  .cost__form-radio {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    pointer-events: none;

    &:checked + .cost__form-custom {
      background: #d64e34;
    }
  }
  .cost__form-custom {
    flex-shrink: 0;
    width: 11px;
    height: 11px;
    border-radius: 2px;
    background: #d2d3d5;
    margin-right: 8px;
    transition: .3s;
  }
  .cost__form-desc {
    line-height: 16px;
  }
  
  .cost__form-price {
    margin-bottom: 0;
    color: #d64e34;
    margin-top: auto;
  }
  .cost__form-submit {
    max-width: 270px;
    width: 100%;
    margin-top: 30px;
  }

  .gift {
    padding-left: 19px;
    padding-top: 15px;
    padding-right: 20px;

    .cost__form-type {
      padding-left: 0;
    }
    .cost__form-desc {
      color: #fff;
    }
  }
  .gift__label {
    display: block;
    &:not(:last-child) {
      margin-bottom: 10px !important;
    }
  }
  .gift__input {
    border-color: #000;
    padding-bottom: 10px;
    color: #000;
    font-size: 10px;
    display: block;
    width: 100%;
  }

  & + .programms {
    padding-top: 75px;
  }
}

