* {
  font-family: "Factor A", sans-serif;
  font-weight: 400;
  font-style: normal;
}

html {
  overflow-x: hidden;
}

body {
  padding-right: 0 !important;
  color: #000;
}

html .--fw700 {
  font-weight: 700;
}

html .--fw500 {
  font-weight: 500;
}

.--uppercase {
  text-transform: uppercase;
}

.page-btn {
  text-decoration: none;
  cursor: pointer;
  outline: none;
  border: none;
  box-shadow: none;
  padding: 18px 50px;
  border: 1px solid #fff;
  font-size: 14px;
  font-weight: 700;
  line-height: 110%;
  text-align: center;
  display: inline-block;
  color: #fff;
}
.page-btn.--orange {
  background: #d64e34;
  border-color: #d64e34;
}
.page-btn.--black {
  background: transparent;
  border: 1px solid #000;
  color: #000;
}
@media screen and (max-width: 1050px) {
  .page-btn {
    padding: 14px 30px;
  }
}

.page-link {
  font-size: 16px;
  font-weight: 700;
  line-height: 120%;
  text-decoration: underline;
  display: flex;
  justify-content: start;
  align-items: center;
}
.page-link img {
  margin-left: 4px;
}

.page-h2 {
  font-size: 28px;
  font-weight: 700;
  line-height: 100%;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 50px;
}
.page-h2 svg {
  position: relative;
  top: 2px;
}
@media screen and (max-width: 1340px) {
  .page-h2 {
    margin-bottom: 40px;
  }
}

.page-text-20 {
  font-size: 20px;
  font-weight: 400;
  line-height: 120%;
}
@media screen and (max-width: 1050px) {
  .page-text-20 {
    font-size: 14px;
  }
}

.page-text-14 {
  font-size: 14px;
  font-weight: 400;
  line-height: 120%;
}

[data-accordion=content] {
  max-height: 0;
  -webkit-transition: max-height 0.3s;
  -o-transition: max-height 0.3s;
  transition: max-height 0.3s;
  overflow: hidden;
}

[data-tabs=element] {
  position: absolute;
  visibility: hidden;
